import { Link } from "gatsby";
import * as React from "react";
import styled from "styled-components";

import LogoDark from "../images/logo_dark.svg";
import { isVosborCn } from "./isCn";
import { media } from "./media-queries";

export const Footer = () => {
  //TODO: refactor `isCN` to the upperst level to select between vosbor.com and vosbor.cn
  const isCN = isVosborCn();
  return (
    <FooterContainer className="even-section">
      <AddressWrapper>
        <AddressList>
          <Address>
            <AddressTitle>Amsterdam</AddressTitle>
            <AddressLine>Piet Heinkade 55</AddressLine>
            <AddressLine>1019 GM, The Netherlands</AddressLine>
            <Phone>+31 20 217 0982</Phone>
          </Address>
          <Address>
            <AddressTitle>Singapore</AddressTitle>
            <AddressLine>79 Robinson Road</AddressLine>
            <AddressLine>Singapore 068897</AddressLine>
            <Phone>+65 6829 2314</Phone>
          </Address>
        </AddressList>
        {isCN && (
          <CnLink href="https://beian.miit.gov.cn/" target="_blank">
            琼ICP备2022012348号-号
          </CnLink>
        )}
      </AddressWrapper>

      <div className="links">
        <FooterLink href="/contact">Contact</FooterLink>
        <FooterLink href="/legal-notice">Legal Notice</FooterLink>
        <FooterLink href="/privacy-policy">Privacy Policy</FooterLink>
        <FooterLink href="/blog">Blog</FooterLink>
      </div>

      <Link
        to="/"
        className="tablet-hidden"
        style={{ minWidth: "360px", textAlign: "right" }}
      >
        <LogoDark />
      </Link>
    </FooterContainer>
  );
};

const FooterContainer = styled.footer`
  padding: 30px 35px 25px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 42px;

  ${media.m`
    padding: 40px 39px 25px;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  `}

  ${media.l`
    padding: 40px 80px 25px;
  `}

  ${media.xl`
    padding: 50px 100px 25px;
  `}
`;

const AddressWrapper = styled.div``;

const AddressList = styled.div`
  display: flex;
  align-items: stretch;
  gap: 40px;
`;

const Address = styled.address`
  display: flex;
  flex-direction: column;
`;

const AddressTitle = styled.h4`
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  line-height: 32px;

  letter-spacing: 0.002em;

  color: #47474f;
  font-size: 16px;

  ${media.m`
    font-size: 18px;
  `}

  ${media.xl`
    font-size: 24px;
  `}
`;

const AddressLine = styled.span`
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  line-height: 32px;

  letter-spacing: 0.002em;

  color: #747487;
  font-size: 14px;

  ${media.l`
    font-size: 16px;
  `}
`;

const Phone = styled.a`
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  line-height: 32px;

  letter-spacing: 0.002em;

  margin-top: 9px;
  text-decoration: none;

  color: #747487;
  font-size: 14px;

  ${media.l`
    font-size: 16px;
  `}
`;

export const FooterLink = styled.a`
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;

  letter-spacing: 0.03em;

  color: #747487;

  text-decoration: none;

  &:not(:last-child)::after {
    content: "/";
    color: #c8c8ca;
    display: inline-block;
    vertical-align: middle;
    opacity: 0.5;

    padding: 0 6px;
    font-size: 12px;
    line-height: 20px;

    ${media.l`
      padding: 0 8px;
    `}

    ${media.l`
      padding: 0 18px;
      font-size: 14px;
      line-height: 26px;
    `}

    ${media.xl`
      font-size: 16px;
      line-height: 28px;
    `}
  }

  ${media.m`
    font-size: 14px;
    line-height: 26px;
  `}

  ${media.xl`
    font-size: 16px;
    line-height: 28px;
  `}
`;

const CnLink = styled.a`
  display: inline-block;
  margin-top: 18px;
  text-decoration: none;

  font-family: Roboto;
  font-size: 12px;
  font-weight: 400;
  line-height: 28px;
  letter-spacing: 0.002em;
  text-align: left;

  ${media.m`
    font-size: 14px;
    line-height: 26px;
  `}

  ${media.xl`
    font-size: 16px;
    line-height: 28px;
  `}
`;
