import { Link } from "gatsby";
import styled, { css } from "styled-components";
import { media } from "../helpers/media-queries";

const buttonStyles = css`
  width: 160px;
  box-sizing: border-box;
  text-decoration: none;
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  padding: 0;

  border-width: 2px;
  border-style: solid;
  border-radius: 4px;

  font-family: "Poppins";
  font-weight: 500;
  font-size: 13px;

  height: ${({ $thin }) => ($thin ? "42px" : "46px")};
  line-height: ${({ $thin }) => ($thin ? "38px" : "42px")};

  ${media.l`
    font-size: 15px;
    height: ${({ $thin }) => ($thin ? "44px" : "48px")};
    line-height: ${({ $thin }) => ($thin ? "40px" : "44px")};
  `}

  ${media.xl`
    font-size: 18px;
    height: ${({ $thin }) => ($thin ? "48px" : "52px")};
    line-height: ${({ $thin }) => ($thin ? "44px" : "48px")};
  `}
`;

export const BaseButtonLink = styled(Link)`
  ${buttonStyles}
`;

export const SubmitButton = styled.button`
  ${buttonStyles}
  cursor: pointer;
  background: #fff;
  border: 2px solid #4200ff;
  color: #4200ff;
  border-radius: 4px;

  &:hover {
    background: #4200ff;
    color: #fff;
  }

  ${media.m`
    align-self: end;
  `}

  &:disabled {
    opacity: 0.5;
  }
`;
