import * as React from "react";
import styled from "styled-components";
import { Link } from "gatsby";

// images as components
import Logo from "../images/logo.svg";
import LogoDark from "../images/logo_dark.svg";
import { media } from "./media-queries";
import { BaseButtonLink } from "../components/ButtonLink";

export const MainHeader = ({ dark = false }) => {
  return (
    <Header>
      <a href="/">{dark ? <LogoDark /> : <Logo />}</a>
      <div className="links">
        <HeaderLink
          id="what_we_do"
          href="/#whatwedo"
          className="separator mobile-hidden"
          $dark={dark}
        >
          What we do
        </HeaderLink>
        <HeaderLink
          id="how_it_works"
          href="/#howitworks"
          className="separator mobile-hidden"
          $dark={dark}
        >
          How it works
        </HeaderLink>
        <HeaderLink
          id="who_we_are"
          href="/#whoweare"
          className="separator mobile-hidden"
          $dark={dark}
        >
          Who we are
        </HeaderLink>
        <HeaderLink id="contact" href="/contact" $dark={dark}>
          Contact
        </HeaderLink>
        <ButtonLink
          id="login_button"
          as="a"
          href="https://exchange.vosbor.com/"
          className="mobile-hidden"
          $dark={dark}
          $thin
        >
          Sign in
        </ButtonLink>
      </div>
    </Header>
  );
};

const Header = styled.header`
  z-index: 1;
  padding: 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;

  .links {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  ${media.m`
    padding: 24px 39px;
  `}

  ${media.l`
    padding: 24px 40px;
  `}

  ${media.xl`
    padding: 24px 68px;
  `}
`;

const HeaderLink = styled.a`
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;

  letter-spacing: 0.03em;

  ${({ $dark }) => {
    return $dark ? `color: #47474F;` : `color: #ffffff;`;
  }}

  text-decoration: none;

  transition-property: color;
  transition-duration: 0.3s;
  &:hover {
    ${({ $dark }) => {
      return $dark
        ? `color: rgb(71 71 79 / 75%);`
        : `color: rgb(255 255 255 / 75%);`;
    }}
  }

  &.separator::after {
    content: "/";
    ${({ $dark }) => {
      return $dark ? `color: #C8C8CA;` : `color: #ffffff;`;
    }}
    ${({ $dark }) => {
      return $dark ? `opacity: 1;` : `opacity: 0.3;`;
    }}
    display: inline-block;
    vertical-align: middle;
    font-family: "Montserrat";
    line-height: 22px;
    padding: 0 16px;
    font-size: 18px;
    ${media.l`
      font-size: 24px;
      padding: 0 24px;
    `}

    &:hover {
      opacity: 1;
    }
  }

  ${media.m`
    font-size: 13px;
  `}

  ${media.l`
    font-size: 15px;
  `}

  ${media.xl`
    font-size: 18px;
  `}
`;

const ButtonLink = styled(BaseButtonLink)`
  border-color: rgba(255, 255, 255, 0.33);
  margin-left: 24px;

  ${media.l`
    margin-left: 50px;
  `}

  ${({ $dark }) => {
    return $dark
      ? `
      color: #47474F;
      border-color: rgba(71, 71, 79, 0.3);
    `
      : `color: #ffffff;`;
  }}

  &:hover,
  &:active {
    background: linear-gradient(270deg, #373cd9 2.12%, #243afa 97.88%);
    border-color: transparent;
    color: #ffffff;
  }
`;
