import { css } from "styled-components";

export const screenSize = {
  xl: 1919,
  l: 1366,
  m: 768,
};

export const media = Object.keys(screenSize).reduce((acc, label) => {
  acc[label] = (...args) => css`
    @media (min-width: ${screenSize[label]}px) {
      ${css(...args)};
    }
  `;
  return acc;
}, {});
